.video-lightbox {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	background: #fff;
	border-radius: 12px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.16);
	overflow: hidden;

	.media-block,
	.components-radio-control {
		padding: 20px;

		/*.components-flex {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-wrap: nowrap;
			flex-direction: row;
			gap: 12px;
			margin-bottom: 12px;
		}*/
	}
	.components-button {
		background-color: #3858e9;
	}

	.video-lb-notes {
		width: 100%;
		padding: 3px 5px;
		text-align: center;
		font-weight: 400;
		text-align: center;
		background-color: var(
			--wp-components-color-accent,
			var(--wp-admin-theme-color, #3858e9)
		);
		color: #fff;
		p {
			margin: 0;
			font-size: 14px;
			font-style: italic;
		}
		h2 {
			font-size: 20px !important;
			margin: 0;
		}
	}
}

.video-thumbnail {
	position: relative;
	background-color: #fff;
}

.vl-button {
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 2px 2px 6px 2px rgba(0, 0, 0, 0.16);
	padding: 0 12px !important;
	height: 36px;
	line-height: 36px;
	transition: 0.5s ease all;
	white-space: nowrap;
	// &:hover {
	// 	background-color: var(
	// 		--wp--preset--color--black,
	// 		var(--wp-admin-theme-color, #000000)
	// 	);
	// 	color: #ffffff;
	// }
}

.vl-thumbnail {
    position: relative;
}

.vl-icon-text-button {
	border-radius: 4px;
	svg {
		width: 24px;
		min-width: 24px;
		margin-right: 8px;
	}
}

.vl-icon-button {
	position: absolute;
	background-color: #fff;
	width: 30px;
	height: 30px;
	display: block;
	right: 8px;
	top: 8px;
	padding: 2px !important;
	svg {
		fill: none;
		position: absolute;
		width: 24px;
		min-width: 24px;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
}
